  height: 100000px;
} */

:where(.css-dev-only-do-not-override-1e5rsp4).ant-input {
  background-color: #EFEFEF;
}

.bg-gradient {
  /* background: linear-gradient(90deg, rgba(121, 79, 153, 1) 22%, rgba(150, 79, 153, 1) 58%, rgba(97, 79, 153, 1) 90%); */
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box;
}

.primary-color {
  color: #774F99
}

.font-bold {
  font-weight: 700;
}


.ant-carousel .slick-prev,
.ant-carousel .slick-next,
.ant-carousel .slick-prev:hover,
.ant-carousel .slick-next:hover {
  font-size: inherit;
  color: white;
}

.ant-carousel .slick-prev,
.ant-carousel .slick-prev:hover {
  left: 10px;
  z-index: 2;
  color: rgb(14, 13, 13);
  font-size: 40px;
}

.ant-carousel .slick-next,
.ant-carousel .slick-next:hover {
  right: 40px;
  z-index: 2;
  /* color: white; */
  color: rgb(14, 13, 13);
  font-size: 40px;


}


:where(.css-dev-only-do-not-override-1e5rsp4).ant-input {
  background-color: #EFEFEF;
}

.bg-gradient {
  /* background: linear-gradient(90deg, rgba(121, 79, 153, 1) 22%, rgba(150, 79, 153, 1) 58%, rgba(97, 79, 153, 1) 90%); */
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box;
}

.primary-color {
  color: #774F99
}

.font-bold {
  font-weight: 700;
}

.carasoul-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;

}

.footer-wrapper {
  background: #EFEFEF;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  gap: 5rem;
  padding: 2rem;
}
.footer-content-wrapper {
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  font-weight: 500;

}

.footer-content-wrapper h2 {
  color: rgb(39, 48, 48);
  font-weight: 700;
  font-size: 1.3rem;
}

.footer-content-wrapper a {
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  color: rgb(39, 48, 48);
}

.footer-content-wrapper:has(.socials) {
  width: 180px;
}

.socials {
  display: flex;
  justify-content: space-between;

}

.socials a {
  font-size: 1.6rem;
}

.office-detail-wrapper {
  width: 700px;
  border-left: 1px solid gray;
  padding-left: 5rem;
}

:where(.css-dev-only-do-not-override-1e5rsp4).ant-row {
  flex-flow: nowrap;
  overflow-x: scroll;
}

:where(.css-dev-only-do-not-override-1e5rsp4).ant-row::-webkit-scrollbar {
  display: none;
}


/* //// new event */
.box-wrapper {
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box;

  padding: 2rem;
}

.head {
  display: flex;
  justify-content: space-between;

}

.news-cards-wrapper{

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 3rem;
  margin: 0 auto;
}

.cardCarasoul {
  /* background: #000; */
  color: white;
  display: flex;
  justify-content: center;
  width: 700px;
}

.slick-prev:before {
  content: '←';
  color: red;
}

.slick-prev:before, .slick-next:before {
  color: black;
  font-size: 25px;
}

:where(.css-dev-only-do-not-override-1e5rsp4).ant-col-6 {
  max-width: none;
}

.slider-wrapper {
  margin: 0 auto;
  padding: 2rem;
}

.cardImg {
  width: 100%;
  height: 280px;
  object-fit: cover;
}

.slick-arrow {
  background: transparent linear-gradient(202deg, #78539C 0%, #6D3085 50%, #393185 100%) 0% 0% no-repeat padding-box !important;
  height: 146px !important;
  width: 59px !important;
  display: flex !important;
  z-index: 1000;
}

.slick-arrow::before {
  content: "❬";
  margin: auto;
  color: white !important;
}

.slick-arrow:hover {
  background: transparent linear-gradient(202deg, #78539C 0%, #6D3085 50%, #393185 100%) 0% 0% no-repeat padding-box !important;
}

.slick-arrow.slick-prev::before {
  content: "❮";
  font-size: 20px;
}

.slick-arrow.slick-next::before {
  content: "❯";
  font-size: 20px;

}

.slick-prev {
  left: -79px;
}

.categoryNav {
  z-index: 10000;
}

.searchBox{
  width: 430px;
}

@media screen and (min-width: 320px) {


  .logo {
    width: 100px;
    margin: 0 1rem;
  }

  .cardImg {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }

  .searchBox, .loginBtn, .profileBtn, .cartBtn, .categoryNav {
    display: none;
  }

  .nav-wrapper {
    justify-content: center;
  }

  .icon-wrapper-header {
    width: 150px;

  }
  :where(.css-dev-only-do-not-override-1e5rsp4).ant-space{
    margin-left: 40px;
  }

  .headerIcon {
    display: block;
    font-size: 1.3rem;
    color: #774F9A;

  }

  .cardCarasoul {
    width: 150px;
  }

  .newsCardCarasoul {
    width: 200px;
  }


  .icon-wrapper-header {
    display: flex;
    gap: 10px;
    justify-content: space-between;
    margin-left: 2.5rem;
  }


  :where(.css-dev-only-do-not-override-1e5rsp4).ant-col-12 {
    display: block;
    flex: 0 0 50%;
    max-width: 100%;
    margin: 0 auto;
  }

  .cardRows {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    /* border: 1px solid green; */

  }

  .box-wrapper {
    overflow-x: scroll;
  }

  .office-detail-wrapper, .footer-content-wrapper {
    border-left: none;
    padding-left: 0;
  }

  .slick-arrow {
    height: 80px !important;
    width: 40px !important;
  }


  .slick-arrow.slick-prev::before {
    content: "❮";
    font-size: 20px;
  }

  .slick-arrow.slick-next::before {
    content: "❯";
    font-size: 20px;

  }

  
}

@media screen and (min-width: 375px) {

  /* .headerIcon {
    display: none;
  } */

  /* .searchBox, .loginBtn, .profileBtn, .cartBtn, .categoryNav {
    display: block;
  } */

  /* .icon-wrapper-header {
    display: none;
  } */


  .icon-wrapper-header {
    width: 200px;

  }

  .slick-arrow {
    height: 100px !important;
    width: 40px !important;
  }


  .slick-arrow.slick-prev::before {
    content: "❮";
    font-size: 25px;
  }

  .slick-arrow.slick-next::before {
    content: "❯";
    font-size: 25px;

  }
}

@media screen and (min-width: 425px) {

  .logo {
    margin-left: 2rem;
  }

  .cardCarasoul {
    width: 350px;
  }

  .icon-wrapper-header {
    width: 240px;

  }
}

@media screen and (min-width: 576px) {

  .logo {
    margin-left: 7rem;
  }

  .icon-wrapper-header {
    width: 340px;
  }

  :where(.css-dev-only-do-not-override-1e5rsp4).ant-space{
    margin-left: 187px;
  }

}

@media screen and (min-width: 992px) {

  .cardRows {
    display: flex;
    flex-direction: row;
    justify-content: space-between;

  }

  .logo {
    margin-left: 11rem;
  }

  .icon-wrapper-header {
    width: 500px;

  }

  /* .navRight{
    width: 700px;
  } */



}

@media screen and (min-width: 1200px) {

  :where(.css-dev-only-do-not-override-1e5rsp4).ant-space{
    margin-left: 0;
  }

  

  .logo {
    margin-left: 0;
    width: 200px;
  }

  .icon-wrapper-header {
    /* width: 340px; */
    display: none;
  }

  .nav-wrapper{
    justify-content: space-between;
  }
  .searchBox, .loginBtn, .profileBtn, .cartBtn {
    display: block;
  }

}


@media screen and (min-width: 1500px) {

  .categoryNav {
    display: block;
  }

  .searchBox{
    width: 600px;
  }

}

